import React, { useState } from 'react';
import { Tooltip } from '@chakra-ui/react';

// Amino Acid Component (Draggable)
const AminoAcid = ({ name, structure, schematic, onDragStart, color }) => {
  return (
    <Tooltip label={`Structure: ${structure}`} aria-label="Amino acid tooltip">
      <div
        draggable
        onDragStart={(e) => onDragStart(e, name)}
        style={{
          cursor: 'move',
          padding: '8px',
          backgroundColor: color,
          borderRadius: '4px',
          margin: '10px',
          textAlign: 'center',
          color: 'white',
          boxShadow: '0px 0px 8px rgba(0, 0, 0, 0.2)',
        }}
      >
        <div>{name}</div>
        <div>{schematic}</div>
      </div>
    </Tooltip>
  );
};

// Puzzle Piece (Droppable Slot)
const PuzzlePiece = ({ position, content, shape, onDrop, onDragOver, highlight }) => {
  const shapeStyles = {
    X: { borderRadius: '0 50% 50% 50%' },
    Y: { borderRadius: '50% 0 50% 50%' },
  };

  return (
    <div
      onDrop={(e) => onDrop(e, position)}
      onDragOver={onDragOver}
      style={{
        height: '100px',
        width: '150px',
        margin: '10px',
        backgroundColor: highlight ? 'lightgreen' : 'white',
        border: '2px solid black',
        textAlign: 'center',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        transition: 'all 0.3s ease-in-out',
        ...shapeStyles[shape],
      }}
    >
      <div>{content || 'Drop Here'}</div>
    </div>
  );
};

// Triple Helix Component
const TripleHelix = ({ isFormed }) => {
  return (
    <div style={{ textAlign: 'center', marginTop: '20px' }}>
      <h3>Triple Helix</h3>
      <div
        style={{
          height: '100px',
          background: isFormed ? 'url("/api/placeholder/100/100")' : 'gray',
          backgroundSize: 'contain',
          backgroundRepeat: 'no-repeat',
          backgroundPosition: 'center',
          transition: 'all 0.5s ease-in-out',
        }}
      >
        {!isFormed && <p>Waiting for correct sequence...</p>}
      </div>
    </div>
  );
};

// CollagenBuilder Component
const CollagenBuilder = () => {
  const [sequence, setSequence] = useState({ X1: null, Y1: null, X2: null, Y2: null, X3: null, Y3: null });
  const [feedback, setFeedback] = useState({ X1: '', Y1: '', X2: '', Y2: '', X3: '', Y3: '' });
  const [isCorrect, setIsCorrect] = useState(false);

  const handleDragStart = (e, aminoAcid) => {
    e.dataTransfer.setData('text/plain', aminoAcid);
  };

  const handleDrop = (e, position) => {
    e.preventDefault();
    const aminoAcid = e.dataTransfer.getData('text');
    const newSequence = { ...sequence, [position]: aminoAcid };
    setSequence(newSequence);
    checkSequence(newSequence);
  };

  const handleDragOver = (e) => {
    e.preventDefault();
  };

  const checkSequence = (newSequence) => {
    let correct = true;
    let newFeedback = { X1: '', Y1: '', X2: '', Y2: '', X3: '', Y3: '' };

    // Validate each X-Y pair based on their positions
    Object.keys(newSequence).forEach((pos) => {
      if (pos.startsWith('X') && newSequence[pos] !== 'Proline') {
        correct = false;
        newFeedback[pos] = 'Proline needed for stability!';
      } else if (pos.startsWith('Y') && newSequence[pos] !== 'Hydroxyproline') {
        correct = false;
        newFeedback[pos] = 'Hydroxyproline needed for hydrogen bonding!';
      }
    });

    setIsCorrect(correct);
    setFeedback(newFeedback);
  };

  return (
    <div style={{ padding: '20px' }}>
      <h2>Build a Collagen Triple Helix</h2>
      <p>Drag the amino acids to their correct positions in the G-X-Y triplet to form a triple helix puzzle.</p>

      <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }}>
        <div style={{ display: 'flex', justifyContent: 'center' }}>
          <PuzzlePiece
            position="X1"
            content={sequence.X1}
            shape="X"
            onDrop={handleDrop}
            onDragOver={handleDragOver}
            highlight={feedback.X1 === ''}
          />
          <PuzzlePiece
            position="Y1"
            content={sequence.Y1}
            shape="Y"
            onDrop={handleDrop}
            onDragOver={handleDragOver}
            highlight={feedback.Y1 === ''}
          />
          <div style={{ padding: '10px' }}>Glycine (Fixed)</div>
        </div>
        <div style={{ display: 'flex', justifyContent: 'center' }}>
          <PuzzlePiece
            position="X2"
            content={sequence.X2}
            shape="X"
            onDrop={handleDrop}
            onDragOver={handleDragOver}
            highlight={feedback.X2 === ''}
          />
          <PuzzlePiece
            position="Y2"
            content={sequence.Y2}
            shape="Y"
            onDrop={handleDrop}
            onDragOver={handleDragOver}
            highlight={feedback.Y2 === ''}
          />
          <div style={{ padding: '10px' }}>Glycine (Fixed)</div>
        </div>
        <div style={{ display: 'flex', justifyContent: 'center' }}>
          <PuzzlePiece
            position="X3"
            content={sequence.X3}
            shape="X"
            onDrop={handleDrop}
            onDragOver={handleDragOver}
            highlight={feedback.X3 === ''}
          />
          <PuzzlePiece
            position="Y3"
            content={sequence.Y3}
            shape="Y"
            onDrop={handleDrop}
            onDragOver={handleDragOver}
            highlight={feedback.Y3 === ''}
          />
          <div style={{ padding: '10px' }}>Glycine (Fixed)</div>
        </div>
      </div>

      <div style={{ marginTop: '20px', display: 'flex', justifyContent: 'center', flexWrap: 'wrap' }}>
        <AminoAcid name="Proline" structure="C5H9NO2" schematic="P" color="red" onDragStart={handleDragStart} />
        <AminoAcid name="Hydroxyproline" structure="C5H9NO3" schematic="HYP" color="green" onDragStart={handleDragStart} />
        <AminoAcid name="Alanine" structure="C3H7NO2" schematic="A" color="orange" onDragStart={handleDragStart} />
        <AminoAcid name="Lysine" structure="C6H14N2O2" schematic="K" color="blue" onDragStart={handleDragStart} />
        <AminoAcid name="Arginine" structure="C6H14N4O2" schematic="R" color="purple" onDragStart={handleDragStart} />
      </div>

      <TripleHelix isFormed={isCorrect} />

      <div style={{ marginTop: '20px', textAlign: 'center' }}>
        {isCorrect ? (
          <p>Congratulations! You've successfully formed a collagen triple helix.</p>
        ) : (
          <p>Keep trying! Remember the unique properties of each amino acid and their role in collagen structure.</p>
        )}
      </div>
    </div>
  );
};

export default CollagenBuilder;
